import React from 'react';
import '../App.css';
import {Link} from 'react-router-dom';



function NavBare() {
    return (
        <div className="nav__bar">
            <h1>Logo</h1>
            <nav>
                <Link to="/">
                <li>Home</li>
                </Link>
               <Link to="/blog"> 
               <li>Blog</li>
               </Link>
                <li><a href="#">About</a></li>
            </nav>
        </div>
    );
}



export default NavBare;